*{
    margin: 0;
    padding: 0;
}
body{
    background: #171717;
    font-family: 'Open Sans', sans-serif;
}

button{
    cursor: pointer;
}

header{
    background: #171616;
    color: #ffffff;
    font-size: 24px;
    padding: 20px 0;

    display: flex;
    justify-content: center;
}
main{
    background: #171717;
    color: #ffffff;
    padding: 0 30px;

    .page-title{
        font-size: 40px;
        padding: 50px 0;
        text-align: center;
    }
    .content{
        display: flex;

        section {
            flex: 1;
        }
        aside {
            min-width: 250px;
        }
    }
    
}

table {
    width: 100%;
    border-collapse: collapse;

    thead {
        tr {
            border-bottom: 3px solid #ccc;
            
            th {
                text-align: left;
                padding-bottom: 16px;
                text-transform: uppercase;
                color: aliceblue;
            }
        }
    }
    tbody {
        tr{
            border-bottom: 3px solid #ccc;

            &:last-child {
                border: 0;
            }

            td{
                padding: 30px 0;
            }
        }

    }
}

.produto {
    display: flex;
    align-items: center;

    img{
        border-radius: 6px;
        max-width: 200;
        max-height: 100;
    }

    .info{
        margin-left: 20px;

    .name {
            font-size: 20px;
            margin-bottom: 10px;
        }
    .categoria {
        color: aliceblue;
        }

    }
}
.qtd {
    background: #ccc;
    display: inline-flex;
    padding: 0 0;
    justify-content: space-around;
    align-items: center;
    min-width: 60px;
    border-radius: 20px;
    overflow: hidden;
    height: 30px;

    span{ 
        margin: 0 10px;
    }

    button {
        display: flex;
        align-items: center;
        background: transparent;
        border: 0;
        padding: 0 10px;
        font-size: 20px;
        height: 100%;

        &:hover {
            background: #eee;
        }

    }
}
.remove {
    background: #ccc;
    border: 0;
    width: 30px;
    height: 30px;
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;

    &:hover {
        background: #eee;
    }
}